export default {
	data() {
		return {
			//
			//share
			share: {
				title: '',
				path: '/pages/index',  //
				imageUrl: '/static/logo.png',  //5:4
			}
		}
	},
	onShareAppMessage(res) { //
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
			success(res) {
				// console.log('success(res)==', res);
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				// console.log('fail(res)==', res);
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
	onShareTimeline(res) { //
		return {
			title: this.share.title,
			path: this.share.path,
			imageUrl: this.share.imageUrl,
			success(res) {
				// console.log('success(res)==', res);
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				// console.log('fail(res)==', res);
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
	// 
	onAddToFavorites(){},
}